import React from 'react';
import logo from './logo.svg';
import './App.css';
import PropTypes from 'prop-types';
import ChatBot, { Loading } from 'react-simple-chatbot';
import axios from 'axios'

URL = "http://68.194.56.25:4240/predict"


var history = [];

class Interfacer extends React.Component{
  constructor(props) {
    super(props);
    this.state = {loading: true, result: <Loading />, trigger: false};
    history.push(props.previousStep.message);
    this.searchInputTimeout = setTimeout(() => {this.makeprediction().then(
      myresponse => {
      this.state.result = myresponse;
      history.push(myresponse);
      this.props.triggerNextStep();
    });
  }, 400);
  }

  addquerytohistory(text) {
    var textarr = [text];
    this.setState((state) => {
      return {history: state.history.concat(textarr)} 
    });
  }

  makeprediction(){
    var tosend = {
      "history" : history,
      "device" : "cpu",
      "top_p" : 0.0,
      "top_k" : 10,
      "temperature" : 1
    }
  
    return axios.post(URL,tosend)
    .then(function(response) {
      return response.data;
    })
    .catch(err=>console.log(err))
  }

  triggetNext() {
    this.setState({ trigger: true }, () => {
      this.props.triggerNextStep();
    });
  }
  render() {
    return(this.state.result);
  }
  
}

Interfacer.propTypes = {
  previousStep: PropTypes.object,
  triggerNextStep: PropTypes.func,
};

Interfacer.defaultProps = {
  steps: undefined,
  triggerNextStep: undefined,
};



var steps = [
  {
    id: '0',
    message: 'Hello there!',
    trigger: '1',
  },
  {
    id: '1',
    message: 'Say something',
    trigger: '2',
  },
  {
    id: '2',
    user: true,
    trigger: '3',
  },
  {
    id: '3',
    component: <Interfacer />,
    trigger: '2',
    waitAction: true,
    asMessage: true
  }

];

function App() {
  return (
    <div className="App">
      <div>
      <ChatBot width="100%" steps={steps} headerTitle="PAL" contentStyle={{ height: '86vh' }} style={{ height: '100%' }} botDelay={750} userDelay={50} />
      </div>
    </div>
  );
}

export default App;
